import {
  clearDomELements,
  onReject,
  renderElementsMarkup,
} from '../utils/utils';
import { fetchProductsByName } from '../api/api';
import { refs } from '../refs/refs';
import { markupSearchingCocktails } from '../templates/markupSearchingCocktails';

export async function getCocktailsByName(e) {
  e.preventDefault();
  const value = e.target.searchinput.value.toLowerCase().trim();
  if (!value) return;
  try {
    const result = await fetchProductsByName(value);

    console.log(result);
    onResolve(result);

    e.target.reset();
  } catch (error) {
    onReject(error);
  }
}

function onResolve(data) {
  const { productList, productListTitle } = refs;
  const { drinks } = data;

  if (!drinks) {
    productListTitle.textContent = "Sorry, we didn't find any cocktail for you";
    clearDomELements(productList);
    return;
  }
  productListTitle.textContent = 'Searching results';
  renderElementsMarkup(productList, markupSearchingCocktails, drinks);
}
